@import '../../../../scss/vendor';


.grid-row{
    border-bottom-style: solid;
    border-width: 1px;
    border-bottom-color: $steel-white;
    min-height:55px;

}

md-checkbox {
   margin: auto;
}



.img-thumb {
    width: 35px;
    height: 35px;
    margin:auto;
}

/* resize images */
.img-thumb img {
    width: 100%;
    height: auto;
    border-style: solid;
    border-color:$steel-white;
    border-width: 1.75px;
}

.garment-name,
.garment-type,
.garment-price,
.garment-inventory{
    margin:auto;
}

input{
    border-width:1px;
    border-style:solid;
    border-color:transparent;
    /* border-color:$steel-white;*/
    padding:5px;
    padding-left:2px;
    margin:10px;
    font-size:1em;
    color:darken($border-reg-color,10%)
}


.garment-price,
.garment-inventory{
input {
    text-align: right;
}
}